var { EN_TYPE, CN_TYPE } = require('../constant')
var en = require('./en')
var cn = require('./cn')

/**
 * @name getLanguage
 * @description 
 * @created 2019102510:38:47
 */
module.exports = function getLanguage(type, languageOption) {
  switch (type) {
    case EN_TYPE:
      return en
    case CN_TYPE:
      return cn
    default:
      return languageOption || {}
  }
}
